<template>
  <form @submit="onSubmit">
    <div class="grid grid-cols-2 xl:grid-cols-3 gap-4 2xl:w-[60%]">
      <div class="form_wrapper my-3">
        <label class="input_label" for="monitoring_status"> Monitoring holati </label>
        <SelectBox id="monitoring_status" v-model="monitoring_status" :options="monitoringTypes" show-by="text" track-by="id" placeholder="Turini tanlang" />
        <span class="error_message">{{ monitoring_statusError }}</span>
      </div>
      <div class="form_wrapper my-3">
        <label class="input_label" for="speciality">Mutaxassisligi</label>
        <input id="speciality" v-model="speciality" placeholder="Mutaxassisligini kiriting" class="base_input" name="speciality" type="text" />
        <span class="error_message">{{ specialityError }}</span>
      </div>
      <div class="form_wrapper my-3">
        <label class="input_label" for="school_type"> Muassasa turi </label>
        <SelectBox id="school_type" v-model="school_type" :options="schoolTypes" show-by="text" track-by="id" placeholder="Turini tanlang" />
        <span class="error_message">{{ school_typeError }}</span>
      </div>
      <div class="form_wrapper my-3">
        <label class="input_label" for="class_group">Sinfi/guruhi</label>
        <input id="class_group" v-model="class_group" placeholder="Sinfi/guruhini kiriting" class="base_input" name="class_group" type="text" />
        <span class="error_message">{{ class_groupError }}</span>
      </div>
      <div class="form_wrapper my-3">
        <label class="input_label" for="class_leader">Sinfi/guruhi rahbari</label>
        <input id="class_leader" v-model="class_leader" placeholder="Sinf rahbari FIO kiriting" class="base_input" name="class_leader" type="text" />
        <span class="error_message">{{ class_leaderError }}</span>
      </div>
      <div class="form_wrapper my-3">
        <label class="input_label" for="address">Yashash joyi (yotoqxona manzili)</label>
        <input id="address" v-model="address" placeholder="Yashash joyini kiriting" class="base_input" name="address" type="text" />
        <span class="error_message">{{ addressError }}</span>
      </div>
      <div class="form_wrapper my-3">
        <label class="input_label" for="mastery">O‘zlashtirishi</label>
        <SelectBox id="mastery" v-model="mastery" :options="masteryLevelList" show-by="text" track-by="id" placeholder="O‘zlashtirishini tanlang" />
        <span class="error_message">{{ masteryError }}</span>
      </div>
      <div class="form_wrapper my-3">
        <label class="input_label" for="character">Hulqi</label>
        <SelectBox id="character" v-model="character" :options="charactersList" show-by="text" track-by="id" placeholder="Hulqini tanlang" />
        <span class="error_message">{{ characterError }}</span>
      </div>
      <div id="deed_and_pictures" class="form_wrapper my-3">
        <label class="input_label"> Asoslovchi o‘rganish dalolatnomasi va rasmlar </label>
        <FileInput id="deed_and_pictures1" v-model:files="deed_and_pictures" :multiple-files="false">
          <template #icon>
            <div class="flex justify-between shadow rounded w-full">
              <p class="flex items-center text-gray-400 px-3 overflow-hidden overflow-ellipsis base_input border-none">Yuklang</p>
              <span class="flex px-4 items-center justify-center rounded bg-primary text-white tracking-wide text-sm cursor-pointer"> YUKLASH </span>
            </div>
          </template>
        </FileInput>
        <span class="error_message">{{ deed_and_picturesError }}</span>
      </div>
      <div class="form_wrapper my-3">
        <label class="input_label" for="is_action_been_taken"> MJtKning 47-moddasi bilan chora ko‘rildimi </label>
        <ButtonGroup id="is_action_been_taken" v-model:selected-item="is_action_been_taken" :titles="btnGroupYesNo" />
        <span class="error_message">{{ is_action_been_takenError }}</span>
      </div>
      <div v-if="hasMJTK47" id="file_action_been_taken" class="form_wrapper my-3">
        <label class="input_label"> Bayonnomani yuklang </label>
        <FileInput id="file_action_been_taken1" v-model:files="file_action_been_taken" :multiple-files="false">
          <template #icon>
            <div class="flex justify-between shadow rounded w-full">
              <p class="flex items-center text-gray-400 px-3 overflow-hidden overflow-ellipsis base_input border-none">Yuklang</p>
              <span class="flex px-4 items-center justify-center rounded bg-primary text-white tracking-wide text-sm cursor-pointer"> YUKLASH </span>
            </div>
          </template>
        </FileInput>
        <span class="error_message">{{ file_action_been_takenError }}</span>
      </div>
    </div>
    <button v-if="isEditMode" class="px-3 m-2 py-2 border bg-primary text-white rounded-md" type="submit" :disabled="isSubmitting">Tahrirlashni tugatish</button>
    <button v-else class="px-3 m-2 py-2 border bg-primary text-white rounded-md" type="submit" :disabled="isSubmitting">Saqlash</button>
  </form>
</template>

<script setup>
import {useField, useForm} from 'vee-validate';
import * as yup from 'yup';
import {isLatinLetters, requiredValidatorMessage} from '../utils/validatorMessages';
import {computed, defineEmits, defineProps, toRefs, watch} from 'vue';
import ButtonGroup from './Common/ButtonGroup';
import {btnGroupYesNo} from '../utils/buttonGroupTitles';
import FileInput from './Common/InputFile/FileInput';
import useSchoolTypes from '../composables/selectbox-list/useSchoolTypes';
import useMonitoringTypes from '../composables/selectbox-list/useMonitoringTypes';
import SelectBox from './Common/SelectBox';
import useCharacters from '../composables/selectbox-list/useCharacters';
import useMasteryLevel from '../composables/selectbox-list/useMasteryLevel';

const emit = defineEmits(['submit']);
const props = defineProps({
  initialFields: {
    type: Object,
    default: function () {
      return null;
    },
  },
  isEditMode: {
    type: Boolean,
    default: false,
  },
  clearFormMode: {
    type: Boolean,
    default: false,
  },
});

const {handleSubmit, isSubmitting, resetForm} = useForm();
const {clearFormMode} = toRefs(props);
const {schoolTypes} = useSchoolTypes();
const {monitoringTypes} = useMonitoringTypes();
const {charactersList} = useCharacters();
const {masteryLevelList} = useMasteryLevel();

watch(
  () => clearFormMode.value,
  (clearFormMode) => {
    if (clearFormMode === true) {
      resetForm();
    }
  }
);

function onInvalidSubmit({values, errors, results}) {
  console.log(Object.keys(errors)[0]);
}

const onSubmit = handleSubmit(async (values) => {
  const formData = new FormData();
  const obj = [];
  Object.entries(values).map(([key, val]) => {
    if (hasMJTK47.value) {
      formData.append(key, val);
      obj.push({[key]: val});
    } else {
      if (key !== 'file_action_been_taken') {
        formData.append(key, val);
        obj.push({[key]: val});
      }
    }
  });
  console.log(obj);
  emit('submit', formData);
}, onInvalidSubmit);

const hasMJTK47 = computed(() => is_action_been_taken.value);

const {value: speciality, errorMessage: specialityError} = useField('speciality', yup.string().test(...isLatinLetters()));
const {value: monitoring_status, errorMessage: monitoring_statusError} = useField('monitoring_status', yup.string().required(requiredValidatorMessage));
const {value: class_group, errorMessage: class_groupError} = useField(
  'class_group',
  yup
    .string()
    .test(...isLatinLetters())
    .required(requiredValidatorMessage)
);
const {value: class_leader, errorMessage: class_leaderError} = useField(
  'class_leader',
  yup
    .string()
    .test(...isLatinLetters())
    .required(requiredValidatorMessage)
);
const {value: address, errorMessage: addressError} = useField(
  'address',
  yup
    .string()
    .test(...isLatinLetters())
    .required(requiredValidatorMessage)
);
const {value: mastery, errorMessage: masteryError} = useField(
  'mastery',
  yup
    .string()
    .test(...isLatinLetters())
    .required(requiredValidatorMessage)
);
const {value: character, errorMessage: characterError} = useField(
  'character',
  yup
    .string()
    .test(...isLatinLetters())
    .required(requiredValidatorMessage)
);
const {value: is_action_been_taken, errorMessage: is_action_been_takenError} = useField('is_action_been_taken', yup.boolean().required(requiredValidatorMessage));

const dynamicValidationForReferenceType = computed(() => (hasMJTK47.value ? yup.mixed().nullable().required(requiredValidatorMessage) : yup.mixed().nullable().notRequired()));

watch(
  () => hasMJTK47.value,
  () => {
    console.log(hasMJTK47.value);
  }
);

const {value: file_action_been_taken, errorMessage: file_action_been_takenError} = useField('file_action_been_taken', dynamicValidationForReferenceType, {
  initialValue: null,
});

const {value: deed_and_pictures, errorMessage: deed_and_picturesError} = useField('deed_and_pictures', yup.mixed().nullable().required(requiredValidatorMessage), {
  initialValue: null,
});

const {value: school_type, errorMessage: school_typeError} = useField('school_type', yup.number().required(requiredValidatorMessage));
</script>

<style scoped></style>
