import axios from "./api/axios";

const MonitoringService = {
  async fetchJuvenilesMonitoringInfo(id) {
    const {data} = await axios.get(`/api/juvenile/monitoring_for_edit/${id}/`);
    return data;
  },
  async editJuvenilesMonitoring(formData, id) {
    return await axios.patch(`/api/juvenile/monitoring_for_edit`, formData, {
      params: {
        juvenile_id: id,
      },
    });
  },
  async addJuvenileToMonitoring (formData, id){
    return await axios.post(`/api/juvenile/juveniles/add_monitoring_info/`, formData, {
      params: {
        juvenile_id: id,
      },
    });
  }
};
export default MonitoringService;
