import {onMounted, ref} from 'vue';
import InfoService from '../../services/info.service';

export default function useMonitoringTypes() {
  const monitoringTypes = ref([]);
  const fetchMonitoringTypes = async () => {
    const {data} = await InfoService.fetchMonitoringTypes();
    monitoringTypes.value = data;
  };
  onMounted(async () => {
    await fetchMonitoringTypes();
  });

  return {
    monitoringTypes,
  };
}
