import { onMounted, ref } from "vue";
import InfoService from "../../services/info.service";

export default function useMasteryLevel() {
  const masteryLevelList = ref([]);
  const fetchMasteryLevelList = async () => {
    const { data } = await InfoService.fetchMasteryLevelList();
    masteryLevelList.value = data;
  };
  onMounted(async () => {
    await fetchMasteryLevelList();
  });

  return {
    masteryLevelList,
    fetchMasteryLevelList,
  };
}
